@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300&display=swap");

:root {
	--mainOpacity: rgba(0, 0, 0, 0.486);
	--blackOpacity: rgba(0, 0, 0, 0.849);
	--white: rgb(255, 255, 255);
	--silver: rgb(199, 199, 199);
	--matteblack: rgb(39, 39, 39);
	--black: rgb(0, 0, 0);
	--darkgrey: rgb(37, 37, 37);
	--deepRed: rgb(153, 0, 0);
}

body {
	margin: 0;
	padding: 0;
	scroll-behavior: smooth;
	overflow-x: hidden;
	font-family: "Barlow Condensed", sans-serif;
}

/* Animations */

@keyframes smoothScroll {
	0% {
		transform: translateY(0);
	}

	100% {
		transform: translateY(-10px);
	}
}

/* Navigation menu styles */
nav {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	background-color: var(--silver);
	box-shadow: 2px 2px 2px 2px var(--mainOpacity);
	color: var(--white);
	padding: 10px;
	position: fixed;
	font-size: 1.5em;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 1;
}

.logo {
	background-image: url(../src/img/cearco_logo.png);
	background-repeat: no-repeat;
}

/* Navigation menu */
.menu {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
	margin: 0;
	padding: 0;
	justify-content: center;
}

.menu li {
	margin: 0 10px;
	margin-left: 20px;
}

.menu a {
	color: var(--matteblack);
	text-decoration: none;
	padding: 20px;
	transition: all 0.5s;
}

.menu a:hover {
	color: var(--white);
	text-decoration: none;
	padding: 20px;
}

/* Hamburger Menu - Main */

.hamburger {
	display: none;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	transition: transform 0.3s;
	margin-left: 10px;
	/* Adjust the left margin as needed */
	padding: 10px;
}

.hamburger span {
	background-color: var(--white);
	box-shadow: 1px 1px 1px var(--mainOpacity);
	display: block;
	height: 3px;
	margin: 3px 0;
	width: 20px;
	transition: transform 0.3s, opacity 0.3s;
}

.menu.open {
	opacity: 1;
	visibility: visible;
	transition: opacity 0.3s, visibility 0s linear 0s;
	position: absolute;
	top: 100%;
	left: 0;
	background-color: var(--matteblack);
	padding: 20px;
	box-shadow: 3px 3px 3px var(--mainOpacity);
}

.menu.open li {
	opacity: 1;
	transform: translateY(0);
	transition: opacity 0.3s, transform 0s linear 0s;
}

.menu.open li:nth-child(1) {
	transition-delay: 0.35s;
}

.menu.open li:nth-child(2) {
	transition-delay: 0.45s;
}

.menu.open li:nth-child(3) {
	transition-delay: 0.55s;
}

.menu.open li:nth-child(4) {
	transition-delay: 0.65s;
}

.hamburger.active span:nth-child(1) {
	transform: rotate(45deg) translate(7px, 5px);
}

.hamburger.active span:nth-child(2) {
	opacity: 0;
}

.hamburger.active span:nth-child(3) {
	transform: rotate(-45deg) translate(7px, -5px);
}

/* Parallax container styles */
.parallax-container {
	position: static !important;
	height: 100vh;
	overflow: hidden;
	margin: 30px;
}

/* Parallax background image styles */
.parallax-bg {
	background-image: url("./img/Black_Background.png");
	background-size: cover;
	background-position: center;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 150vh;
	z-index: -1;
	transform: translateZ(-1px) scale(-1);
}

.parallax-bg2 {
	background-color: var(--silver);
	background-size: cover;
	background-position: center;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -2;
	transform: translateZ(-1px) scale(2);
}

/* Content section styles */
.content {
	position: absolute;
	font-size: 2.5em;
	padding: 5px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
	text-shadow: 5px 5px 5px var(--mainOpacity);
	color: var(--white);
	margin: auto;
	max-width: 800px;
	/* Keeps the width of the paragraph or sentences stable when on desktop */
}

.content h1 {
	font-size: 2em;
	
}

.img-prod {
	border-radius: 30px;
}

strong {
	font-size: 1.5em;
}

/* Button styles */
.learn-more-01 {
	background-color: var(--white);
	color: var(--matteblack);
	text-decoration: none;
	border: none;
	padding: 10px 15px;
	font-size: 0.7em;
	font-family: "Barlow Condensed", sans-serif;
	border-radius: 5px;
	box-shadow: 3px 3px 3px var(--blackOpacity);
	margin-top: 20px;
	cursor: pointer;
	transition: background-color 0.5s;
	animation: smoothScroll 0.5s ease-in-out forwards;
}

.learn-more-01:hover {
	color: var(--white);
	background-color: var(--matteblack);
}

#about {
	background-image: linear-gradient(60deg,
			var(--matteblack),
			var(--blackOpacity)),
		url(../src/img/ourhistory_bg_01.jpg);
	background-size: cover;
	background-position: 0%, 10%;
	background-attachment: fixed;
	height: auto !important;
	width: 100%;
	background-repeat: no-repeat;
	padding-top: 5%;
	padding-bottom: 5%;
	z-index: -1;
}

.about-title {
	font-size: 3.5em;
	text-align: center;
	color: var(--white);
	text-shadow: 3px 3px 3px var(--mainOpacity);
}

.about-paragraph {
	font-size: 1.7em;
	padding: 4vh;
	color: var(--white);
	margin: auto;
	max-width: 800px;
	/* Keeps the width of the paragraph or sentences stable when on desktop */
}

/* Pricing Table */
#product {
	background-image: url(../src/img/prod_bg_01.jpg);
	background-size: cover;
	background-position: 0%, 50%;
	background-attachment: fixed;
	height: auto !important;
	width: 100%;
	background-repeat: no-repeat;
	opacity: var(--mainOpacity);
	padding-top: 5%;
	padding-bottom: 5%;
}

.product-paragraph {
	text-align: center;
	text-shadow: 12px 12px 12px var(--mainOpacity);
	color: var(--black);
	justify-content: center;
	font-size: 2.5em;
	padding-top: 0;
	padding-bottom: 0;
}

.product-table {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
	/* Create a grid with auto-fit columns */
	grid-gap: 20px;
	/* Add gap between grid items */
	justify-items: center;
	/* Center the grid items horizontally */
	align-items: stretch;
	/* Stretch the grid items vertically */
	z-index: 1;
	transform: scale(0.77);
	margin: 0;

}

.product-plan {
	background-image: linear-gradient(60deg, var(--mainOpacity), var(--silver));
	margin: 10px;
	padding: 20px;
	border-radius: 10px;
	background-color: var(--white);
	font-family: "Barlow Condensed", sans-serif;
	justify-content: center;
	box-shadow: 5px 5px 5px var(--mainOpacity);
}

.product-plan h2 {
	padding-left: 0;
	font-size: 2.3em;
	margin-bottom: 0.5em;
}

.product-plan .features {
	list-style: none;
	margin: 0;
	padding: 0;
	text-align: left;
}

.product-plan .features li {
	margin-bottom: 20px;
}

.product-plan .btn {
	display: inline-block;
	padding: 10px 20px;
	font-family: "Barlow Condensed", sans-serif;
	font-size: 1.5em;
	background-color: var(--matteblack);
	color: var(--white);
	text-decoration: none;
	border-radius: 5px;
	transition: background-color 0.5s;
	cursor: pointer;
}

.product-plan .btn:hover {
	background-color: var(--silver);
	color: var(--matteblack);
}

.product-card {
	display: none;
	background-color: var(--white);
	padding: 10px;
	border-radius: 5px;
	font-size: 1.35em;
	padding-left: 20px;
}

.product-plan .btn:focus+.product-card {
	display: block;
}

/* Footer */

.site-footer {
	
	background-color: var(--matteblack);
	padding: 50px;
	font-size: 18px;
	line-height: 24px;
	color: var(--silver);
}

.site-footer hr {
	border-top-color: #bbb;
	opacity: 0.5;
}

.site-footer hr.small {
	margin: 20px 0;
}

.site-footer h6 {
	color: #fff;
	font-size: 16px;
	text-transform: uppercase;
	margin-top: 5px;
	letter-spacing: 2px;
}

.site-footer a {
	color: #737373;
}

.site-footer a:hover {
	color: #3366cc;
	text-decoration: none;
}

.footer-links {
	padding-left: 0;
	list-style: none;
}

.footer-links li {
	display: block;
}

.footer-links a {
	color: var(--silver);
}

.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
	color: var(--deepRed);
	text-decoration: none;
}

.footer-links.inline li {
	display: inline-block;
}

.site-footer .social-icons {
	text-align: right;
}

.site-footer .social-icons a {
	width: 40px;
	height: 40px;
	line-height: 40px;
	margin-left: 6px;
	margin-right: 0;
	border-radius: 100%;
	background-color: #33353d;
}

.copyright-text {
	margin: 0;
}

/* Scrollbar Webkit*/

::-webkit-scrollbar {
	width: 12px;
}

::-webkit-scrollbar-track {
	background-color: var(--mainOpacity);
}

::-webkit-scrollbar-thumb {
	background-color: var(--matteblack);
}

::-webkit-scrollbar-thumb:hover {
	background-color: var(--darkgrey);
}

/* Media queries for mobile */

@media (max-width: 768px) {
	main {
		padding-top: 5vh;
		padding-bottom: 20vh;
	}

	/* Media - Navigation menu */
	.menu {
		width: 100%;
		padding-top: 60px;
		display: none;
		/* Hide menu by default */
	}

	.menu.open {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		background-color: var(--silver);
		padding: 20px;
		position: absolute;
		top: 100%;
		left: 0;
		opacity: 1;
		visibility: visible;
		transition: opacity 0.3s, visibility 0s linear 0s;
	}

	.menu li {
		margin: 10px 0;
	}

	.menu li:hover {
		background-color: var(--darkgrey);
		width: 100%;
		padding: 5px;
	}

	/*  Hamburger Menu - Media  */
	.hamburger {
		display: flex;
		padding-right: 30px;
	}

	.hamburger span {
		background-color: var(--white);
	}

	/*  Parallax container  */

	.parallax-container {
		height: auto;
	}

	/*  Media content  */
	.content {
		position: static;
		transform: none;
		font-size: 1.9em;
	}

	/**** Image content *****/
	.img-prod {
		width: 100%;
		height: auto;
	}


	/*  Product content  */
	#product {
		padding-top: 0.5%;
		padding-bottom: 0.5%;
	}

	.product-table {
		grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
		grid-gap: 20px;
		transform: scale(1);
		margin: 0;
	}

	.product-plan {
		margin: 10px;
		padding: 10px;
		font-size: 1.2em;
	}

	.product-plan h2 {
		font-size: 1.8em;
	}

	.product-card {
		padding: 30px;
		font-size: 1.1em;
	}
}

/* Footer */

.site-footer {
	padding-bottom: 0;
}

.site-footer .copyright-text,
.site-footer .social-icons {
	text-align: center;
}

.social-icons {
	padding-left: 0;
	margin-bottom: 0;
	list-style: none;
}

.social-icons li {
	display: inline-block;
	margin-bottom: 4px;
}

.social-icons li.title {
	margin-right: 15px;
	text-transform: uppercase;
	color: #96a2b2;
	font-weight: 700;
	font-size: 13px;
}

.social-icons a {
	background-color: #eceeef;
	color: #818a91;
	font-size: 16px;
	display: inline-block;
	line-height: 44px;
	width: 44px;
	height: 44px;
	text-align: center;
	margin-right: 8px;
	border-radius: 100%;
	-webkit-transition: all 0.2s linear;
	-o-transition: all 0.2s linear;
	transition: all 0.2s linear;
}

.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
	color: var(--white);
	background-color: var(--deepRed);
}

.social-icons.size-sm a {
	line-height: 34px;
	height: 34px;
	width: 34px;
	font-size: 14px;
}

.social-icons a.facebook:hover {
	background-color: var(--silver);
}

.social-icons a.instagram:hover {
	background-color: var(--deepRed);
}

@media (max-width: 767px) {
	.social-icons li.title {
		display: block;
		margin-right: 0;
		font-weight: 600;
	}
}